}


// Emitters:
// @updated-by-user
// @removed-by-user


const data = function() {
	let data = {}
	data.i = this.item
	data.encrypted_switcher = this.item.isEncrypted()
	// data.dragged = false
	// data.draging_over = false
	// data.dragged_id = null
	return data
}


