	i: {
		deep: true,
		handler() {
			console.log('cambio!')
			this.updated()
		},
	},
	encrypted_switcher: function(to, from) {
		if (to === false) {
			this.decrypt()
			this.encrypted_switcher = false
		} else if (to === true) {
			this.encrypt()
			this.encrypted_switcher = true
		}
	},
}


